import * as React from "react";
import * as Sentry from "@sentry/browser";
import * as BDTLL from "../../BDTLL_common";
import "./errorBoundary.css"

interface IErrorBoundaryState {
    error: string,
    errorInfo: any,
    eventId: string,
    theme: string
}

export class ErrorBoundary extends React.Component <{}, IErrorBoundaryState> {
    constructor(props: any) {
        super(props);
        this.state = {
            error: null,
            errorInfo: null,
            eventId: null,
            theme: null
        } as IErrorBoundaryState;
    }

    componentDidCatch(error: any, errorInfo: any) {
        this.setState({
            error: error,
            errorInfo: errorInfo,
        });

        if (process.env.NODE_ENV !== "development") {
            Sentry.withScope((scope: any) => {
                scope.setExtra("Error stack", error.stack.toString());
                scope.setExtras(errorInfo);
                scope.setExtra("Extension version", BDTLL.Consts.VERSION);
                const eventId = Sentry.captureException(error);
                this.setState({eventId})
            });   
        }
    }

    componentDidMount() {
        window.matchMedia('(prefers-color-scheme: dark)').addListener(({ matches }) => {
            this.setState({
                theme: matches ? 'dark' : 'light'
            });
        });
    }

    async openFeedback() {
        window.location.href = BDTLL.Utils.getExtensionUrl("pages/feedback/feedback.html")
    }

    render() {
        if (this.state.errorInfo) {
            // Error path
            return (
                <div>
                    <div className="container_top">
                        <div className="tl_logo">
                            <div id="web_protection_text">{BDTLL.Utils.get_localized_text("web_protection_text")}</div>
                            <div id="company_name"> Bitdefender</div>
                        </div>
                    </div>
                    <div className="error_container">
                        <div className="error_title" id="error_title">Something went wrong!</div>
                        <div className="footer_feedback_note">
                            <span>{BDTLL.Utils.get_localized_text("footer_feedback_note")}</span>
                            <a href='#' id='hui_feedback' onClick={this.openFeedback}>{BDTLL.Utils.get_localized_text("link_feedback")}</a>
                        </div>
                    </div>
                </div>
            );
        }

        return this.props.children;
    }
}