import * as BDTLL from "../BDTLL";

export class Update {
	storage: BDTLL.IStorage;
	scanner: BDTLL.IScanner;
	assl: BDTLL.Iassl;
	interval = 14400000; // 4 hours between updates

	constructor(storage: BDTLL.IStorage, scanner: BDTLL.IScanner, assl: BDTLL.Iassl) {
		this.storage = storage;
		this.scanner = scanner;
		this.assl = assl;
		this.checkUpdate();
		setInterval(this.checkUpdate, this.interval);
		this.listen();
	}

	async checkUpdate() {
		try {
			var currentTime = (new Date()).getTime();
			var storedTime = parseInt(await this.storage.get('time'));

			if (!(isNaN(storedTime) || storedTime + this.interval < currentTime))
				return 0;

			this.storage.set('time', currentTime);
			this.update().catch((reason: any) => {
				// can't update: check reason
			});
		} catch (err) { }
	}

	async update() {
		var date = new Date();
		this.storage.set('date', date.toLocaleDateString());

		this.requestInfo("ph_sign.slf").then(async (xhr: XMLHttpRequest) => {
			this.assl.sign = new Array();
            this.assl.nosign = new Array();
            this.storage.set("slfContent", xhr.responseText);
            var version = await this.assl.createRegexes();
		});

		this.requestInfo("ph_white.txt").then((xhr: XMLHttpRequest) => {
            this.storage.set("internalWhitelist", xhr.responseText).then(() => {
				this.scanner.internalWhitelist = xhr.responseText.split("\n");
			});
		});
	}

	async requestInfo(filename: string) {
		var sURL = 'https://nimbus.bitdefender.net/tll/update?file=' + filename;
		var oRequest = BDTLL.Utils.xHTTPRequest();
		oRequest.nativeXhr.open("GET", sURL, true);
		if (BDTLL.BrowserConsts) {
			oRequest.nativeXhr.setRequestHeader("X-Nimbus-ClientId", BDTLL.BrowserConsts.BROWSER_CLIENTID || "");
		}

		return oRequest.send();
	}

	listen() {
		BDTLL.MessageService.addListener(
			(message: any, sender: any) => {
				if (message.command == BDTLL.Command.GET_LAST_UPDATE)
					return this.storage.get("date");
			}
		);
	}
}