import * as BDTLL from "../BDTLL";

export class CloudTalk {
	static extractVerdict(item: any) {
		var index = BDTLL.StatusPriority.indexOf(BDTLL.PageStatus.SAFE);

		if (item.status_message instanceof Array) {
			index = Math.max(index, ...item.status_message.map((status: string) => {
				return BDTLL.StatusPriority.indexOf(status as BDTLL.PageStatus);
			}));
		}
		else
			index = Math.max(BDTLL.StatusPriority.indexOf(item.status_message as BDTLL.PageStatus), index);

		return BDTLL.StatusPriority[index];
	}

	static async interogateCloud(url: string | string[]) {
		let response: any = await new Promise((resolve) => {
			var cloudURI = "https://nimbus.bitdefender.net/url/status";
			var xhr = new XMLHttpRequest();
			xhr.open("POST", cloudURI, true);
			xhr.setRequestHeader("Content-Type", "application/json");
			if (BDTLL.BrowserConsts) {
				xhr.setRequestHeader("X-Nimbus-ClientId", BDTLL.BrowserConsts.BROWSER_CLIENTID || "");
			}

			var requestData;
			if (url instanceof Array) {
				requestData = url.map(item => { return { url: item }});
				xhr.setRequestHeader("X-Is-Batch", "1");
			} else {
				requestData = {
					url: url
				};
			}

			xhr.onload = function() {
				resolve(xhr.responseText);
			};
			xhr.onerror = function () {
				console.error(`** An error occurred during the XMLHttpRequest: ${xhr.statusText}`);
			  	resolve(undefined);
			};

			let stringData = JSON.stringify(requestData);

			xhr.send(stringData);
		});

		var respObj = JSON.parse(response);
		if (respObj instanceof Array) {
			return respObj.map(item => this.extractVerdict(item));
		} else {
			return this.extractVerdict(respObj);
		}
  	}
}